.mapouter{
  position:relative;
  text-align:right;
  width:100%;
  height:400px;
  margin-top: 40px;
  margin-bottom: 40px;
  border-radius: 10px;
  border-collapse: collapse;
}

.gmap_canvas {
  overflow:hidden;
  background:none!important;
  width:100%;
  height:400px;
}

.gmap_iframe {
  height:400px!important;
}