body {
  margin: 0;
  font-family: "Lusitana", serif;
  background-color: #D9CCC3;
  color: black;
}

.material-symbols-rounded {
  user-select: none;
  font-variation-settings:
  'FILL' 1,
  'wght' 500,
  'GRAD' 0,
  'opsz' 24;
}

.links-generic > a{
  color: inherit;
  text-decoration: none;
}

.links-generic > a:hover {
  text-decoration: underline;
}